/* eslint-disable max-len */
import React, { forwardRef } from 'react';
import generalImg from '../db/generalImg.json';
import sFinData from '../db/sFinData.json';
import sHillsData from '../db/sHillsData.json';

import Pins from '../components/Pins';

function PinContainer({
  posX,
  posY,
  posScale,
  mapChange,
  pinChanger,
  cabinMap,
  cabinFocus,
  addTransition,
  setImageChange,
  finfocusSpot,
  finPinsHandler,
  finPinsState,
  hillsPinsState,
  hillsfocusSpot,
  hillsPinsHandler,
}, ref) {
  const {
    containerRef,
    pinContainerRef,
    finfacRef,
    hillsfacRef,
  } = ref;
  return (
    <div
      className="map-container"
      style={
        {
          transformOrigin: '0 0',
          transform: `translate(${posX}px, ${posY}px) scale(${posScale})`,
        }
      }
      ref={containerRef}
      /* onWheel={(e) => {
        onScroll(e);
        finPinsHandler();
        hillsPinsHandler();
      }}
      onDragStart={(e) => {
        resetTransition();
        dragStart(e);
      }}
      onDrag={drag}
      onDragEnd={dragEnd}
      onDragOver={(e) => { e.preventDefault(); }}
      draggable
      */
    >
      <div
        className={`
        ${mapChange === 'home' ? 'sono' : ''}
        ${mapChange === 'tab1' ? 'sono sono-fin' : ''}
        ${mapChange === 'tab2' ? 'sono sono-hills' : ''}
        `}
      >
        <div
          className={`
            s-fin-pin-wrap
            ${pinChanger === 'fin-hover' ? 'show-s-fin-pin-wrap' : ''}
            ${pinChanger === 'fin-cabin-mode' ? 'move-s-fin-pin-wrap' : ''}
          `}
          ref={mapChange === 'tab1' ? pinContainerRef : null}
        >
          <Pins
            data={sFinData}
            pinState={finPinsState}
            addTransition={addTransition}
            setImageChange={setImageChange}
            focusSpot={finfocusSpot}
            pinsHandler={finPinsHandler}
            mapChange={mapChange}
            mainTab="tab1"
            classList="s-fin-pin"
            ref={(el) => { finfacRef.current.push(el); }}
          />
          <img
            className="fin-zoning"
            src={process.env.PUBLIC_URL + generalImg.images[0].fin_zoning}
            alt="Fin zoning"
          />
          <div className={
            `masterplan-cabin
            ${cabinMap === true ? 'show-masterplan-cabin' : ''}`
          }
          >
            {generalImg.images[0].masterplan_cabin.map((list, index) => (
              <img
                className={`
                  masterplan-cabin-img
                  masterplan-${list.cabinTitle}-img
                  ${cabinFocus === index + 1 ? 'focus-masterplan-cabin-img' : ''}
                  ${cabinFocus === 4 ? 'all-masterplan-cabin-img' : ''}
                  `}
                src={process.env.PUBLIC_URL + list.cabinImg}
                alt={list.cabinTitle}
              />
            ))}
          </div>
        </div>
        {/*
          <div
            className={pinChanger === 'fin-hover' ? 's-fin-fac-pin-wrap show-s-fin-fac-pin-wrap' : 's-fin-fac-pin-wrap'}
          >
          {sFinFacPins}
        </div>
        */}

        <div
          className={`
            s-hills-pin-wrap
            ${pinChanger === 'hills-hover' ? 'show-s-hills-pin-wrap' : ''}
          `}
          ref={mapChange === 'tab2' ? pinContainerRef : null}
        >
          <Pins
            data={sHillsData}
            pinState={hillsPinsState}
            focusSpot={hillsfocusSpot}
            pinsHandler={hillsPinsHandler}
            addTransition={addTransition}
            setImageChange={setImageChange}
            mapChange={mapChange}
            mainTab="tab2"
            classList="s-hills-pin"
            ref={(el) => { hillsfacRef.current.push(el); }}
          />
        </div>

        <div
          className={`
            layer
            layer-left
            ${mapChange === 'tab1' ? 'show-layer-left' : ''}
            ${pinChanger === 'fin-cabin-mode' ? 'move-layer-left' : ''}
          `}
        />
        <div
          className={`layer layer-right ${mapChange === 'tab2' ? 'show-layer-right' : ''}`}
        />
        <div className={`background-map ${pinChanger === 'fin-cabin-mode' ? 'move-background-map' : ''}`}>
          <img src={process.env.PUBLIC_URL + generalImg.images[0].masterplan_dk} alt="sono fin map" />
        </div>
      </div>
    </div>
  );
}

export default forwardRef(PinContainer);
