import React, { forwardRef } from 'react';
import modular from '../db/modular.json';

function CabinChart({
  pos,
  mapChange,
  cabinFocus,
  cabinFocusHandler,
  showCabinMap,
  setPos,
  setFinPinsState,
  cabinHandler,
  setPinChanger,
}) {
  const posOrigin = (posX, posY, posScale) => {
    setPos({ x: posX, y: posY, scale: posScale });
  };

  return (
    <div className={
      `modular-info-chart ${pos.scale > 1.5 || mapChange !== 'tab1' ? 'hide-modular-info-chart' : ''}`
      }
    >
      <div className="modular-info-table">
        {modular.info.map((item, index) => (
          <div className="modular-info-row" key={item.title}>
            <div
              className={`
                info
                ${item.title !== undefined ? 'info-title' : ''}
                ${cabinFocus === index ? 'focus-info' : ''}
                copy-body-small
              `}
              onClick={() => {
                cabinFocusHandler(index);
                showCabinMap(true);
                setFinPinsState(false);
                posOrigin(0, 0, 1);
                cabinHandler(index - 1);
                setPinChanger('fin-cabin-mode');
              }}
              role="presentation"
            >
              <p>
                {item.title}
              </p>
            </div>
            <div className={`
              info
              info-shape
              ${cabinFocus !== undefined ? 'hide-info' : ''}
              `}
            >
              {item.shape === undefined
                ? ''
                : <img src={process.env.PUBLIC_URL + item.shape} alt={item.title} />}
            </div>
            <div className={`
              info
              info-keys
              copy-body-small
              ${cabinFocus !== undefined ? 'hide-info' : ''}
              `}
            >
              <p>
                {item.keys}
              </p>
            </div>
            <div className={`
              info
              info-percent
              copy-body-small
              ${cabinFocus !== undefined ? 'hide-info' : ''}
              `}
            >
              <p>
                {item.percent}
              </p>
            </div>
            <div className={`
              info
              info-g-a-key
              copy-body-small
              ${cabinFocus !== undefined ? 'hide-info' : ''}
              `}
            >
              <p>
                {item.g_a_key === undefined ? '' : item.g_a_key}
              </p>
            </div>
            <div className={`
              info
              info-total-g-a
              copy-body-small
              ${cabinFocus !== undefined ? 'hide-info' : ''}
              `}
            >
              <p>
                {item.total_g_a}
              </p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

export default forwardRef(CabinChart);
