import {
  React,
  useEffect,
  useState,
} from 'react';

import {
  Routes,
  Route,
  useLocation,
} from 'react-router-dom';

import {
  BrowserView,
  MobileView,
} from 'react-device-detect';

import Intro from './router/Intro';
import Home from './router/Home';

import './style.scss';

function App() {
  const location = useLocation();
  const [currentLocation, setCurrentLocation] = useState(location);
  const [transitionStage, setTransitionStage] = useState('fadeIn');

  useEffect(() => {
    if (location !== currentLocation) setTransitionStage('fadeOut');
  }, [location, currentLocation]);

  return (
    <div className="main">
      <BrowserView>
        <div
          className={`${transitionStage}`}
          onAnimationEnd={() => {
            if (transitionStage === 'fadeOut') {
              setTransitionStage('fadeIn');
              setCurrentLocation(location);
            }
          }}
        >
          <Routes location={currentLocation}>
            <Route path="/" element={<Intro />} />
            <Route path="/home" element={<Home />} />
            {/* <Route path="/home/fin-brand-story" element={<FinBrandStory />} /> */}
          </Routes>
        </div>
      </BrowserView>
      <MobileView>
        <div className="mobile-env">
          <h1 className="copy-sub-heading">
            This page is rendered only in browser
          </h1>
        </div>
      </MobileView>
    </div>
  );
}

export default App;
