/* eslint-disable max-len */
import React, { useState, useRef, useEffect } from 'react';

import Navigation from '../components/Navigation';
import CabinChart from '../components/CabinChart';

import PinContainer from '../templates/PinContainer';
import DetailInfo from '../templates/DetailInfo';
import FinBrandStoryVideo from '../templates/FinBrandStoryVideo';
import HillsBrandStory from '../templates/HillsBrandStory';
// import AreaPin from './templates/AreaPin';

import generalImg from '../db/generalImg.json';
import sFinData from '../db/sFinData.json';
import sHillsData from '../db/sHillsData.json';
import modular from '../db/modular.json';

function MainBanner() {
  // let posX = 0;
  // let posY = 0;

  // DOM Management

  const refs = {
    pinContainerRef: useRef(),
    infoRef: useRef(),
    containerRef: useRef(),
    finVideoRef: useRef(),
    hillsVideoRef: useRef([]),
    finfacRef: useRef([]),
    hillsfacRef: useRef([]),
  };

  const [finVideo, showFinVideo] = useState(false);
  const [hillsStory, showHillsStory] = useState(false);

  useEffect(() => {
    if (finVideo) {
      refs.finVideoRef.current.play();
    } else {
      refs.finVideoRef.current.pause();
    }
  });

  // State Management
  const [theme, setTheme] = useState('dk');

  const [cabinMap, showCabinMap] = useState(false);
  const [cabinFocus, setCabinFocus] = useState();
  const cabinFocusHandler = (index) => { setCabinFocus(index); };

  const [mapChange, setMapChange] = useState('home');
  const [pinChanger, setPinChanger] = useState();
  const [expanding, setExpanding] = useState('');
  const [finPinsState, setFinPinsState] = useState();
  const [hillsPinsState, setHillsPinsState] = useState();
  const [cabinState, setCabinState] = useState();
  const [imageChange, setImageChange] = useState(0);
  const [pos, setPos] = useState({ x: 0, y: 0, scale: 1 });

  const finPinsHandler = (index) => { setFinPinsState(index); };
  const hillsPinsHandler = (index) => { setHillsPinsState(index); };
  const cabinHandler = (index) => { setCabinState(index); };

  const imageChangeHandler = (index) => { setImageChange(index); };

  const themeChange = () => {
    if (theme === 'dk') {
      setTheme('lt');
    } else {
      setTheme('dk');
    }
  };

  const resetContainer = () => {
    refs.containerRef.current.style.top = '0px';
    refs.containerRef.current.style.left = '0px';
  };

  const addTransition = () => {
    refs.containerRef.current.style.transition = '1s ease-in-out';
  };

  const zoomScale = 2.5;
  const finfocusSpot = (e) => {
    resetContainer();

    pos.x = (refs.pinContainerRef.current.offsetLeft * -(zoomScale))
      + (refs.finfacRef.current[e].offsetLeft * -(zoomScale))
      + (refs.finfacRef.current[e].offsetWidth * -1)
      + (refs.containerRef.current.offsetWidth / 4);

    pos.y = (refs.pinContainerRef.current.offsetTop * -(zoomScale))
        + (refs.finfacRef.current[e].offsetTop * -(zoomScale))
        + ((refs.finfacRef.current[e].offsetHeight * -1))
        + (refs.containerRef.current.offsetHeight / 2);

    pos.scale = zoomScale;
    showCabinMap(false);
  };

  const hillsfocusSpot = (e) => {
    resetContainer();

    pos.x = (refs.pinContainerRef.current.offsetLeft * -(zoomScale))
      + (refs.hillsfacRef.current[e].offsetLeft * -(zoomScale))
      + (refs.hillsfacRef.current[e].offsetWidth * -1)
      + (refs.containerRef.current.offsetWidth / 4);

    pos.y = (refs.pinContainerRef.current.offsetTop * -(zoomScale))
        + (refs.hillsfacRef.current[e].offsetTop * -(zoomScale))
        + ((refs.hillsfacRef.current[e].offsetHeight * -1))
        + (refs.containerRef.current.offsetHeight / 2);

    pos.scale = zoomScale;
    showCabinMap(false);
  };

  // resetTransition
  /*
  const resetTransition = () => {
    containerRef.current.style.transition = 'none';
  };
  */

  // Drag & zoom in out
  /*
  const posDefault = () => {
    pos.x = 0;
    pos.y = 0;
    pos.scale = 1;
    containerRef.current.style.top = '0px';
    containerRef.current.style.left = '0px';
  };

  const onScroll = (e) => {
    const delta = e.deltaY * -0.001;
    const newScale = pos.scale + delta;

    const ratio = 1 - newScale / pos.scale;

    if (pos.scale < 1) {
      posDefault();
      addTransition();
    } else {
      setPos({
        x: pos.x + (e.clientX - pos.x) * ratio,
        y: pos.y + (e.clientY - pos.y) * ratio,
        scale: newScale,
      });
    }

    resetTransition();
  };

  const dragStart = (e) => {
    const img = new Image();
    e.dataTransfer.setDragImage(img, 0, 0);

    posX = e.clientX;
    posY = e.clientY;
  };

  const drag = (e) => {
    e.target.style.left = `${e.target.offsetLeft + (e.clientX - posX)}px`;
    e.target.style.top = `${e.target.offsetTop + (e.clientY - posY)}px`;

    posX = e.clientX;
    posY = e.clientY;
  };

  const dragEnd = (e) => {
    e.target.style.left = `${e.target.offsetLeft + (e.clientX - posX)}px`;
    e.target.style.top = `${e.target.offsetTop + (e.clientY - posY)}px`;

    posX = e.clientX;
    posY = e.clientY;
  };
*/

  // const wheelHandler = (e) => {setRatio(ratio => (ratio >= 0.1 ? ratio + 0.001 * e.deltaY : 0.2));}

  // const disappear
  return (
    <div
      className={`map-wrapper th-${theme}-bg`}
      // eslint-disable-next-line react/no-unknown-property
    >
      <div
        className="theme-changer"
        role="presentation"
        onClick={themeChange}
      >
        change theme
      </div>

      <FinBrandStoryVideo
        showFinVideo={showFinVideo}
        finVideo={finVideo}
        ref={refs.finVideoRef}
      />

      <HillsBrandStory
        showHillsStory={showHillsStory}
        hillsStory={hillsStory}
        ref={refs.hillsVideoRef}
      />

      <PinContainer
        posX={pos.x}
        posY={pos.y}
        posScale={pos.scale}
        mapChange={mapChange}
        pinChanger={pinChanger}
        theme={theme}
        cabinMap={cabinMap}
        cabinFocus={cabinFocus}
        showCabinMap={showCabinMap}
        addTransition={addTransition}
        setImageChange={setImageChange}
        finfocusSpot={finfocusSpot}
        hillsfocusSpot={hillsfocusSpot}
        finPinsHandler={finPinsHandler}
        hillsPinsHandler={hillsPinsHandler}
        finPinsState={finPinsState}
        hillsPinsState={hillsPinsState}
        ref={refs}
      />

      <Navigation
        pos={pos}
        expanding={expanding}
        mapChange={mapChange}
        setMapChange={setMapChange}
        addTransition={addTransition}
        setPos={setPos}
        setPinChanger={setPinChanger}
        setImageChange={setImageChange}
        finfocusSpot={finfocusSpot}
        hillsfocusSpot={hillsfocusSpot}
        finPinsHandler={finPinsHandler}
        setFinPinsState={setFinPinsState}
        sFinData={sFinData}
        hillsPinsHandler={hillsPinsHandler}
        finPinsState={finPinsState}
        hillsPinsState={hillsPinsState}
        cabinState={cabinState}
        sHillsData={sHillsData}
        theme={theme}
        showCabinMap={showCabinMap}
        setCabinFocus={setCabinFocus}
        showVideo={showFinVideo}
        showHillsStory={showHillsStory}
      />

      <CabinChart
        pos={pos}
        mapChange={mapChange}
        setPinChanger={setPinChanger}
        cabinFocus={cabinFocus}
        showCabinMap={showCabinMap}
        cabinFocusHandler={cabinFocusHandler}
        setFinPinsState={setFinPinsState}
        setPos={setPos}
        cabinMap={cabinMap}
        setCabinFocus={setCabinFocus}
        finPinsHandler={finPinsHandler}
        setMapChange={setMapChange}
        cabinHandler={cabinHandler}
      />

      <DetailInfo
        mapChange={mapChange}
        data={sFinData}
        pinsState={finPinsState}
        setPos={setPos}
        pinsHandler={finPinsHandler}
        addTransition={addTransition}
        setExpanding={setExpanding}
        setImageChange={setImageChange}
        expanding={expanding}
        imageChange={imageChange}
        imageChangeHandler={imageChangeHandler}
        generalImg={generalImg}
        mapTab="tab1"
        ref={refs.infoRef}
      />

      <DetailInfo
        mapChange={mapChange}
        data={sHillsData}
        pinsState={hillsPinsState}
        setPos={setPos}
        pinsHandler={hillsPinsHandler}
        addTransition={addTransition}
        setExpanding={setExpanding}
        setImageChange={setImageChange}
        expanding={expanding}
        imageChange={imageChange}
        imageChangeHandler={imageChangeHandler}
        generalImg={generalImg}
        ref={refs.infoRef}
        mapTab="tab2"
      />

      <DetailInfo
        setPinChanger={setPinChanger}
        mapChange={mapChange}
        data={modular}
        pinsState={cabinState}
        setPos={setPos}
        pinsHandler={cabinHandler}
        finPinsHandler={finPinsHandler}
        addTransition={addTransition}
        setExpanding={setExpanding}
        setImageChange={setImageChange}
        expanding={expanding}
        imageChange={imageChange}
        imageChangeHandler={imageChangeHandler}
        generalImg={generalImg}
        setCabinFocus={setCabinFocus}
        showCabinMap={showCabinMap}
        ref={refs.infoRef}
        mapTab="tab1"
      />
      <div className="confidential">
        <p className="copy-confidential copy-body-large">
          Confidential
        </p>
      </div>
    </div>
  );
}

export default MainBanner;
