import React, { forwardRef } from 'react';
import finBrandStoryData from '../db/finBrandStory.json';
import generalImage from '../db/generalImg.json';

import CloseBtn from '../components/CloseBtn';

function FinBrandStoryVideo({
  finVideo,
  showFinVideo,
}, ref) {
  return (
    <div className={`video-container ${finVideo === true ? 'show-video-container' : ''}`}>
      <CloseBtn
        generalImage={generalImage}
        onClickHandler={() => {
          showFinVideo(false);
          // eslint-disable-next-line react/destructuring-assignment
        }}
      />
      <video
        className="fin-brand-story-video"
        controls
        src={process.env.PUBLIC_URL + finBrandStoryData.finBrandStoryVideo}
        ref={ref}
      >
        <track kind="captions" />
      </video>
    </div>
  );
}

export default forwardRef(FinBrandStoryVideo);
