import React, { forwardRef } from 'react';
// import { Link } from 'react-router-dom';

function Navigation({
  pos,
  expanding,
  mapChange,
  setMapChange,
  addTransition,
  setPos,
  setPinChanger,
  setImageChange,
  finfocusSpot,
  hillsfocusSpot,
  finPinsHandler,
  sFinData,
  hillsPinsHandler,
  finPinsState,
  hillsPinsState,
  sHillsData,
  theme,
  showCabinMap,
  showVideo,
  showHillsStory,
  cabinState,
}) {
  const posOrigin = (posX, posY, posScale) => {
    setPos({ x: posX, y: posY, scale: posScale });
  };

  return (
    <div
      className={`
        map-swiper
        ${pos.scale > 1.5 ? 'hide-swiper' : ''}
        ${cabinState !== undefined ? 'hide-swiper' : ''}
        ${expanding === 'expand' ? 'hide-swiper' : ''}`}
    >
      <ul>
        <li
          className={
            `th-${theme}-body swiper ${mapChange === 'home' ? 'active' : ''}`
          }
          onClick={() => {
            setMapChange('home');
            addTransition();
            posOrigin(0, 0, 1);
            showCabinMap(false);
          }}
          role="presentation"
        >
          <i className={
            `th-${theme}-pointer-bg marker ${mapChange === 'home' ? 'active' : ''}`
            }
          />
          MASTERPLAN
        </li>
        <li
          className={
            `th-${theme}-body swiper ${mapChange === 'tab1' ? 'active' : ''}`
          }
          onClick={() => {
            setMapChange('tab1');
            finPinsHandler();
            hillsPinsHandler();
            posOrigin(0, 0, 1);
          }}
          onMouseEnter={() => { setPinChanger('fin-hover'); }}
          onMouseLeave={() => { setPinChanger(); }}
          role="presentation"
        >
          SONO FIN VIVALDI
          <i className={`marker ${mapChange === 'tab1' ? 'active' : ''}`} />
        </li>
        <ul className={`area-list ${mapChange === 'tab1' ? 'area-list-max' : ''}`}>
          <li
            onClick={() => {
              showVideo(true);
            }}
            role="presentation"
          >
            0. Brand Story
          </li>
          {sFinData.dataList.map((list, index) => (
            <li
              className={finPinsState === index ? 'list-selected' : ''}
              onClick={() => {
                addTransition();
                setImageChange(0);
                finfocusSpot(index);
                finPinsHandler(index);
                showCabinMap(undefined);
              }}
              role="presentation"
            >
              {`${index + 1}. ${list.name}`}
            </li>
          ))}
        </ul>
        <li
          className={
            `th-${theme}-body swiper ${mapChange === 'tab2' ? 'active' : ''}`
          }
          onClick={() => {
            setMapChange('tab2');
            finPinsHandler();
            hillsPinsHandler();
            showCabinMap(false);
            posOrigin(0, 0, 1);
          }}
          onMouseEnter={() => { setPinChanger('hills-hover'); }}
          onMouseLeave={() => { setPinChanger(); }}
          role="presentation"
        >
          <i className={`marker ${mapChange === 'tab2' ? 'active' : ''}`} />
          SONO HILLS
        </li>
        <ul className={`area-list ${mapChange === 'tab2' ? 'area-list-max' : ''}`}>
          <li
            onClick={() => {
              showHillsStory(true);
            }}
            role="presentation"
          >
            0. Brand Story
          </li>
          {sHillsData.dataList.map((list, index) => (
            <li
              className={hillsPinsState === index ? 'list-selected' : ''}
              onClick={() => {
                addTransition();
                setImageChange(0);
                hillsfocusSpot(index);
                hillsPinsHandler(index);
              }}
              role="presentation"
            >
              {`${index + 1}. ${list.name}`}
            </li>
          ))}
        </ul>
      </ul>
    </div>
  );
}

export default forwardRef(Navigation);
