import React, { forwardRef } from 'react';

function Pins({
  data,
  pinState,
  pinsHandler,
  addTransition,
  setImageChange,
  focusSpot,
  mapChange,
  mainTab,
  classList,
}, ref) {
  const pins = data.dataList.map((list, index) => (
    <div
      className={pinState === index ? `${classList}-selected ${classList}` : classList}
      ref={ref}
      role="presentation"
      onClick={() => {
        addTransition();
        setImageChange(0);
        focusSpot(index);
        pinsHandler(index);
      }}
      key={list.name}
    >
      <div className={pinState !== undefined ? 'hide-pin pin' : 'pin'}>
        <p className={`pin-head ${mapChange === mainTab ? 'show-pin-head' : ''}`} />
        <div className={`pin-line ${mapChange === mainTab ? 'show-pin-line' : ''}`} />
        <div className={`pin-icon ${mapChange === mainTab ? 'show-pin-icon' : ''}`}>
          <img src={process.env.PUBLIC_URL + list.icon} alt={`${list.name} icon`} />
        </div>
        <div className={`pin-title ${mapChange === mainTab ? 'show-pin-title' : ''}`}>
          {`${index + 1}. ${list.name}`}
        </div>
      </div>
      <img
        className="area-mark"
        src={process.env.PUBLIC_URL + list.mark}
        alt={list.name}
      />
    </div>
  ));
  return (
    [pins]
  );
}

export default forwardRef(Pins);
